

/* main css */
:root{
  --white-color: #fff;
  --black-color: #000;
  --text-color: #333;
  --size-title: 4.8rem;
  --size-text: 1.8rem;
}

*{
  box-sizing:inherit ;
  margin: 0;
  list-style-type: none;
  text-decoration: none;
}

html{
  font-size: 62.5%;
  line-height: 1.6rem;
  box-sizing: border-box;
  font-family: Montserrat,"Helvetica Neue";
}

.grid__full-width{
  max-width: 100%;
}

.grid{
  max-width: 100%;
  margin: 0 auto;
  width: 1920px;
}

.grid__row{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}   

.grid__column-2-4{
  width: 20%;
  padding: 0 5px;
}

.grid__column-3{
  width: 25%;
  padding: 0 20px;
}

.grid__column-4{
  width: 33.3333%;
  padding: 0 10px;
}


/* header */
.header{
  height: 80px;
  padding: 24px 120px 27px 97px;
  background-color: #111;
  display: flex;
  align-items:center;
  justify-content: space-between;
}

.header__logo{
  width: 228px;
  height: 28px;
  object-fit: contain;
  position: relative;
  bottom:2px;
}

.header__icon-menu{
  font-size: 2.8rem;
  color: #fff;
  display: none;
}

.header__navbar{
  position: relative;
}

.header__navbar::after{
  content: "";
  display: block;
  height: 2rem;
  width: 0.5px;
  position: absolute;
  top: calc(50% - 12px) ;
  left: 63.5%;
  background-color:var(--white-color);
}

.navbar__item{
  display: inline-block;
}

.navbar__item-link{
  color: var(--white-color);
  font-size: 1.4rem;
  line-height: 1.4rem;
  margin-left: 38px;
  font-weight: normal;
  letter-spacing: normal;
  opacity: 0.95;
}

.navbar__item-link:hover{
  opacity: 0.7;
}

/* banner  */
.banner-img{
  width:100%;
  object-fit: cover;
}


/* introduction */

#introduction{
  position:relative;
  top: -1vh;
  
}

.introduction__img{
  width: 100%;
}

#introduction .introduction__text {
  position:absolute;
  top: 0%;
  left: 0%;
}

#introduction .introduction__text h2{
  font-size: var(--size-title);
  font-weight: 800;
  color: var(--white-color);
  margin: 120px 16px 0px 173px;
  letter-spacing: 4px;
}

#introduction .introduction__text >span{
  margin: 42px 16px 132px 173px;
  line-height: 4rem;
  width: 580px;
  font-size: var(--size-text);
  color: var(--white-color);
  display: block;
  
  
}

/* best product */
.best-product{
