.btn {
  position: relative;
  border-radius: 3px;
  cursor: pointer;
  background-color: #00BED8;
  color:var(--white-color);
  border: none;
  width: 190px;
  height: 54px;
  font-weight: 800;
  font-size: var(--size-text);
}

.btn:before, .btn:after {
 content: "";
 display: block;
 position: absolute;
 background-color: rgba(255, 255, 255, 0.4);
 height: 50%;
 width: 0;
 transition: all 0.25s linear;
}

.btn::before{
  top: 0;
  left: 0;
  right: auto;
}

.btn:hover::before {
  width: 100%;
  right: 0;
  left: auto;
}

.btn::after{    
 bottom: 0;
 left: auto;
  right: 0;
}

.btn:hover::after {
  width: 100%;
  left: 0;
  right: auto;
}

